//Favorites styles
.favorites {

    &__text {
        text-align: center;
        margin-bottom: 60px;

        @include media-query(tablet) {
                font-size: 16px;
                line-height: calc(19 / 16);
            }
     }

    &__list {

            @extend %list;

            display: grid;
            grid-gap: $indent;
            grid-template-columns: repeat(1, 1fr);

        @include media-query(tablet) {    
                grid-template-columns: repeat(2, 1fr);
            }
        
            @include media-query(desktop) {
                grid-template-columns: repeat(3, 1fr);
            }       
    }

    &__item {
        display: inline-flex;
        align-items: center;
        justify-content: center;
    }

    &__item--none {
        @include media-query(tablet-less) {
            display: none;
        }
        
    }


    &__image {
        border: 11px solid $color-yellow;
        border-radius: 50%;
        padding: 18px 17px;
        overflow: hidden;

        transition: border-color $cubic;

        @include media-query(tablet) {
                border: 14px solid $color-yellow;
                padding: 21px 20px;
            }
        
            @include media-query(desktop) {
                border: 16px solid $color-yellow;
                padding: 25px 24px;
            }       
    }

    &__image:hover {
        border-color: $color-green;
    }

}